<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix card-header">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="" name="first" style="width: 100%;">

        </el-tab-pane>
        <!-- end -->
        <el-tab-pane label="" name="second" style="width: 100%;">

        </el-tab-pane>
      </el-tabs>
      <div id="first" v-show="firstone">
        <el-form :inline="true" :model="searchForm" label-width="80px">
          <el-form-item label=''>
            <el-select @change=getDate(searchForm.type) v-model='searchForm.type'>
              <el-option label='' value='-8' key='-8'></el-option>
              <el-option label='' value='-9' key='-9'></el-option>
              <el-option label='' value='0' key='0'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item v-show="yearcheck">
            <el-select v-model='searchForm.year' style="width: 120px">
              <el-option v-for="item in yearList" :label='item.year' :value='item.year' :key='item.id'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item v-show="monthcheck">
            <el-select v-model='searchForm.month' style="width: 120px">
              <el-option v-for="item in monthList" :label='item.month' :value='item.month' :key='item.id'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button @click='getList()' type='primary' plain></el-button>
          </el-form-item>
        </el-form>
        <!--  -->
        <div class="charts" style="width: 100%">
          <div id="oChart" :style="{width:'100%',height:'400px'}"></div>
        </div>
        <!-- end -->
      </div>
      <div id="second" v-show="secondtwo">
        <el-form :inline="true" :model="shopForm" label-width="100px">
          <!--  -->
          <el-form-item label=''>
            <el-select @change=getOne(shopForm.one) v-model='shopForm.one'>
              <el-option label='' value='-1' key='-1'></el-option>
              <el-option v-for="item in onelist" :label='item.uname' :value='item.id' :key='item.id'></el-option>
            </el-select>
          </el-form-item>
          <!--  / -->
          <el-form-item v-show="agenttwo">
            <el-select @change=getTwo(shopForm.two) v-model='shopForm.two' placeholder="" style="width: 120px">
              <el-option label='/' value='2' key='2'></el-option>
              <el-option label='' value='3' key='3'></el-option>
              <el-option label='' value='4' key='4'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item v-show="agentthree">
            <el-select @change=getThree(shopForm.three) v-model='shopForm.three' style="width: 150px">
              <el-option :label='msgthree' value='-1' key='-1'></el-option>
              <el-option v-for="item in threelist" :label='item.uname' :value='item.id' :key='item.id'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item v-show="agentfour">
            <el-select @change=getFour(shopForm.four) v-model='shopForm.four' style="width: 120px">
              <el-option label='' value='-1' key='-1'></el-option>
              <el-option v-for="item in fourlist" :label='item.name' :value='item.id' :key='item.id'></el-option>
            </el-select>
          </el-form-item>
          <span v-show="nodata" class="el-form-item__label" style="color: #409EFF"></span>
        </el-form>
        <!--  -->
        <el-form :inline="true" :model="searchshopForm" label-width="100px">
          <el-form-item label=''>
            <el-select @change=getsecondDate(searchshopForm.stype) v-model='searchshopForm.stype'>
              <el-option label='' value='-8' key='-8'></el-option>
              <el-option label='' value='-9' key='-9'></el-option>
              <el-option label='' value='0' key='0'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item v-show="syearcheck">
            <el-select v-model='searchshopForm.syear' style="width: 120px">
              <el-option v-for="item in yearList" :label='item.year' :value='item.year' :key='item.id'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item v-show="smonthcheck">
            <el-select v-model='searchshopForm.smonth' style="width: 120px">
              <el-option v-for="item in monthList" :label='item.month' :value='item.month' :key='item.id'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select @change=getsumDate(searchshopForm.sales) v-model='searchshopForm.sales' style="width: 120px">
              <el-option label='' value='-2' key='-2'></el-option>
              <el-option label='' value='-3' key='-3'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button @click='getShopData()' type='primary' plain></el-button>
          </el-form-item>
        </el-form>
        <!-- end -->
        <!--  -->
        <div class="charts" style="width: 100%;">
          <div id="oChart2" :style="{width:'100%',height:'400px'}"></div>
        </div>
        <!-- end -->
      </div>
    </el-card>
  </div>
</template>

<script>
  import qs from 'qs'
  import domain from '../../../static/js/url.js'
  export default {
    name: 'App',
    data() {
      return {
        activeName: 'first',
        yearcheck: true, //
        monthcheck: true, //
        syearcheck: true, //
        smonthcheck: true, //
        nowyear: '', //
        nowday: '', //
        shareList: [], //
        yearList: [], //
        monthList: [], //
        syearList: [], //
        smonthList: [], //
        datecheck: '-8', //     
        shopdatecheck:'-8',//     
        salescheck:'-2',
        // 
        searchForm: {
          type: '-8',
          year: '',
          month: '',
        },
        // 
        searchshopForm: {
          stype: '-8',
          syear: '',
          smonth: '',
          sales: '-2'
        },
        couponSelected: '',
        xAxis: [], //X
        shopxAxis:[], //X
        //
        // 
        otype: '', //
        onelist: [],   // 
        threelist: [], //
        fourlist: [], //
        agenttwo: false,
        agentthree: false,
        agentfour: false,
        nodata: false,
        msgthree: '',
        shopForm: {
          one: '-1',
          two: '',
          three: '',
          four: '-1'
        },
        agencyid: '',  //id
        type: '',  //
        agencyoneid: '', //id
        saleid: '', //id
        clinicid: '', //id
        goodsid: '',  //id
        sumamount: 'amount', //
        timeway: '',  //
        datetime: '', //
        secondtwo: true,
        firstone: true,
        fourName:''
      };
    },
    activated() {

    },
    methods: {
      handleClick(tab) {
        this.activeName = tab.name
        if (tab.name == 'second') {
          this.allAgent()
          this.getShopData()
          this.secondtwo = true
          this.firstone = false
        }else{
          this.secondtwo = false
          this.firstone = true
        }
      },
      // 
      getList() {
        let _this = this
        let datecheck = this.datecheck
        let da = {}
        console.log(datecheck)
        console.log(this.searchForm)
        if (datecheck == '-8') {
          (da.time_way = 'day'),
            (da.date_time =
              this.searchForm.year.split('')[0] +
              '-' +
              this.searchForm.month.split('')[0] +
              '-' +
              this.nowday);
        } else if (datecheck == '-9') {
          (da.time_way = 'month'),
            (da.date_time =
              this.searchForm.year.split('')[0] +
              '-' +
              this.searchForm.month.split('')[0] +
              '-' +
              this.nowday)
        } else if (datecheck == '0') {
          (da.time_way = 'year'), (da.date_time = '')
        }
        this.$axios.post(
          domain.testUrl + '/admin/report_forms/statistics',
          qs.stringify(da),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        ).then(res => {
          console.log(res)
          if (res.data.code == '200') {
            let oChart = this.$echarts.init(document.getElementById('oChart'))
            oChart.setOption({
              tooltip: {
                trigger: 'axis'
              },
              legend: {
                data: ['', '', '']
              },
              xAxis: [
                {
                  type: 'category',
                  boundaryGap: false,
                  data: this.xAxis
                }
              ],
              yAxis: {},
              series: [
                {
                  name: '',
                  type: 'line',
                  color: '#37a2da',
                  smooth: true,
                  data: res.data.data.sign_in
                },
                {
                  name: '',
                  type: 'line',
                  color: '#67e0e3',
                  smooth: true,
                  data: res.data.data.share
                },
                {
                  name: '',
                  type: 'line',
                  color: '#ff9f7f',
                  smooth: true,
                  data: res.data.data.sign_in
                }
              ]
            },window.onresize = oChart.resize)
          } else {
            this.sureLoading = false
            _this.$Notice.warning({ title: res.data.message })
          }
        })
      },
      //  
      getDate(datecheck) {
        var _this = this
        _this.xAxis.splice(0, _this.xAxis.length)  //
        this.datecheck = datecheck
        if (datecheck == '-8') {
          // 
          this.yearcheck = true
          this.monthcheck = true
          var date = new Date()
          var year = date.getFullYear()
          var month = date.getMonth() + 1
          var d = new Date(year, month, 0)
          d = d.getDate()
          for (let j = 1; j <= d; j++) {
            _this.xAxis.push(j)
          }
        } else if (datecheck == '-9') {
          // 
          this.yearcheck = true
          this.monthcheck = false
          let date = new Date()
          let year = date.getFullYear()
          for (let i = 2018; i < year; i++) {
            this.yearList.push({ id: i, year: i + '' })
          }
          // 
          for (let j = 1; j < 13; j++) {
            _this.xAxis.push(j)
          }
        } else if (datecheck == '0') {
          // 
          this.yearcheck = false
          this.monthcheck = false
          //  
          let date = new Date()
          let year = date.getFullYear()
          for (let i = 2018; i <= year; i++) {
            _this.xAxis.push(i)
          }
        }
      },
      // 
      getsecondDate(shopdatecheck){
        var _this = this
        _this.shopxAxis.splice(0, _this.shopxAxis.length)  //
        this.shopdatecheck = shopdatecheck
        console.log(shopdatecheck)
        if (shopdatecheck == '-8') {
          // 
          this.syearcheck = true
          this.smonthcheck = true
          var date = new Date()
          var year = date.getFullYear()
          var month = date.getMonth() + 1
          var d = new Date(year, month, 0)
          d = d.getDate()
          for (let j = 1; j <= d; j++) {
            _this.shopxAxis.push(j)
          }
        } else if (shopdatecheck == '-9') {
          // 
          this.syearcheck = true
          this.smonthcheck = false
          // 
          for (let j = 1; j < 13; j++) {
            _this.shopxAxis.push(j)
          }
        } else if (shopdatecheck == '0') {
          // 
          this.syearcheck = false
          this.smonthcheck = false
          //  
          let date = new Date()
          let year = date.getFullYear()
          for (let i = 2018; i <= year; i++) {
            _this.shopxAxis.push(i)
          }
        }
      },
      getsumDate(salescheck){
      if(salescheck == '-2'){
        this.sumamount = 'amount'
        this.fourName = ''
      }else if(salescheck == '-3'){
        this.sumamount = 'sum'
        this.fourName = ''
      }
      },
      // 
      getYearMonth() {
        let _this = this
        _this.xAxis.splice(0, _this.xAxis.length)  //
        _this.shopxAxis.splice(0, _this.shopxAxis.length)  //
        var date = new Date()
        var year = date.getFullYear()
        var month = date.getMonth() + 1
        this.searchForm.year = year + ''
        this.searchForm.month = month + ''
        this.searchshopForm.syear = year + ''
        this.searchshopForm.smonth = month + ''
        this.nowday = date.getDate()
        var d = new Date(year, month, 0)
        d = d.getDate()
        this.yearList = [{ id: '1', year: year + '' }]
        for (let i = 1; i < 13; i++) {
          if (month === i) {
            this.monthList.push({ id: '-1', month: i + '' })
          } else {
            this.monthList.push({ id: i, month: i + '' })
          }
        }
        for (let j = 1; j <= d; j++) {
          this.xAxis.push(j)
          this.shopxAxis.push(j)
        }
        this.getList()
        this.secondtwo = false
      },
      // 
      allAgent() {
        let _this = this
        let da = {}
        this.$axios.post(
          domain.testUrl + '/admin/report_forms/get_general_agency',
          qs.stringify(da),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        ).then(res => {
          if (res.data.code == '200') {
            (_this.onelist = res.data.data)
            console.log(this.onelist)
          } else {
            _this.sureLoading = false
            _this.$Notice.warning({ title: res.data.message })
          }
        })
      },
      // 
      getOne(type) {
        console.log(type)
        if (type == '-1') {
          (this.agenttwo = false),
          (this.agentthree = false),
          (this.agentfour = false),
          (this.shopForm.two = ''),
          (this.shopForm.three = ''),
          (this.type = ''),
          (this.clinicid = ''),
          (this.goodsid = '')
        } else {
          this.agencyid = type
          this.agenttwo = true
        }
        this.nodata = false
      },
      getTwo(type) {
        if (type == '2') {
          this.zensuoData()
          this.msgthree = '/'
          this.type = 'clinic'
        }
        if (type == '3') {
          this.xiaoshouData()
          this.msgthree = ''
          this.type = 'sale'
        }
        if (type == '4') {
          this.dailiData()
          this.msgthree = ''
          this.type = 'agency'
        }
        this.otype = type
        console.log("" + this.otype)
      },
      getThree(type) {
        console.log("" + this.otype)
        console.log(type)
        if (this.otype == '2') {
          let shopid = ''
          if (type != '-1') {
            shopid = type
          }
          this.zensoushopData(shopid)
          this.clinicid = shopid
        }
        if (this.otype == '3') {
          let shopid = ''
          if (type != '-1') {
            shopid = type
          }
          this.saleid = shopid
          this.xiaoshoushopData(shopid)
        }
        if (this.otype == '4') {
          let shopid = ''
          if (type != '-1') {
            shopid = type
          }
          this.agencyoneid = shopid
          this.xiaoshoushopData(shopid)
        }
      },
      getFour: function (type) {
        let shopid = ''
        if (type != '-1') {
          shopid = type
        }
        this.goodsid = shopid
        console.log('ID' + type)
      },
      zensuoData() {
        let _this = this
        let da = {}
        da.agency_id = this.agencyid
        this.$axios.post(
          domain.testUrl + '/admin/report_forms/get_clinic_all',
          qs.stringify(da),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        ).then(res => {
          if (res.data.code == '200') {
            (_this.threelist = res.data.data)
            if (res.data.data.length > 0) {
              (_this.agentthree = true),
                (_this.nodata = false)
            } else {
              (_this.agentthree = false),
                (_this.nodata = true)
            }
            console.log(this.onelist)
          } else {
            (_this.agentthree = false)
            _this.sureLoading = false
            _this.$Notice.warning({ title: res.data.message })
          }
        })
      },
      xiaoshouData() {
        let _this = this
        let da = {}
        da.agency_id = this.agencyid
        this.$axios.post(
          domain.testUrl + '/admin/report_forms/get_salesman_all',
          qs.stringify(da),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        ).then(res => {
          if (res.data.code == '200') {
            (_this.threelist = res.data.data)
            if (res.data.data.length > 0) {
              (_this.agentthree = true),
                (_this.nodata = false)
            } else {
              (_this.agentthree = false),
                (_this.nodata = true)
            }
            console.log(this.onelist)
          } else {
            (_this.agentthree = false)
            _this.sureLoading = false
            _this.$Notice.warning({ title: res.data.message })
          }
        })
      },
      dailiData() {
        let _this = this
        let da = {}
        da.agency_id = this.agencyid
        this.$axios.post(
          domain.testUrl + '/admin/report_forms/get_agency_all',
          qs.stringify(da),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        ).then(res => {
          if (res.data.code == '200') {
            (_this.threelist = res.data.data)
            if (res.data.data.length > 0) {
              (_this.agentthree = true),
                (_this.nodata = false)
            } else {
              (_this.agentthree = false),
                (_this.nodata = true)
            }
            console.log(this.onelist)
          } else {
            (_this.agentthree = false)
            _this.sureLoading = false
            _this.$Notice.warning({ title: res.data.message })
          }
        })
      },
      // 
      zensoushopData(shopid) {
        let _this = this
        let da = {}
        da.agency_id = this.agencyid
        da.clinic_id = shopid
        this.$axios.post(
          domain.testUrl + '/admin/report_forms/get_clinic_goods',
          qs.stringify(da),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        ).then(res => {
          console.log(res)
          if (res.data.code == '200') {
            (_this.fourlist = res.data.data)
            if (res.data.data.length > 0) {
              (_this.agentfour = true),
                (_this.nodata = false)
            } else {
              (_this.agentfour = false),
                (_this.nodata = true)
            }
            console.log(this.onelist)
          } else {
            (_this.agentfour = false)
            _this.sureLoading = false
            _this.$Notice.warning({ title: res.data.message })
          }
        })
      },
      // 
      xiaoshoushopData(shopid) {
        let _this = this
        let da = {}
        da.agency_id = this.agencyid
        da.salesman_id = shopid
        this.$axios.post(
          domain.testUrl + '/admin/report_forms/get_salesman_goods',
          qs.stringify(da),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        ).then(res => {
          console.log(res)
          if (res.data.code == '200') {
            (_this.fourlist = res.data.data)
            if (res.data.data.length > 0) {
              (_this.agentfour = true),
                (_this.nodata = false)
            } else {
              (_this.agentfour = false),
                (_this.nodata = true)
            }
            console.log(this.onelist)
          } else {
            (_this.agentfour = false)
            _this.sureLoading = false
            _this.$Notice.warning({ title: res.data.message })
          }
        })
      },
      // 
      dalishopData(shopid) {
        let _this = this
        let da = {}
        da.agency_id = this.agencyid
        da.agency_one_id = shopid
        this.$axios.post(
          domain.testUrl + '/admin/report_forms/get_agency_all_goods',
          qs.stringify(da),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        ).then(res => {
          console.log(res)
          if (res.data.code == '200') {
            (_this.fourlist = res.data.data)
            if (res.data.data.length > 0) {
              (_this.agentfour = true),
                (_this.nodata = false)
            } else {
              (_this.agentfour = false),
                (_this.nodata = true)
            }
            console.log(this.onelist)
          } else {
            (_this.agentfour = false)
            _this.sureLoading = false
            _this.$Notice.warning({ title: res.data.message })
          }
        })
      },
      // 
      getShopData() {
        let _this = this
        let da = {}
        da.agency_id = this.agencyid
        da.type = this.type
        console.log('id' + this.agencyid)
        console.log('' + this.type)
        console.log('23.4.' + this.otype)
        if (this.otype == '2') {
          da.clinic_id = this.clinicid
          console.log('id' + this.clinicid)
        }
        if (this.otype == '3') {
          da.sale_id = this.saleid
          console.log('id' + this.saleid)
        }
        if (this.otype == '4') {
          da.agency_one_id = this.agencyoneid
          console.log('id' + this.agencyoneid)
        }
        console.log('id' + this.goodsid)
        da.goods_id = this.goodsid
        console.log('' + this.sumamount)
        da.sum_amount = this.sumamount
        console.log('123' + this.shopdatecheck)
        let datecheck = this.shopdatecheck;
        if (datecheck == '-8') {
          (da.time_way = 'day'),
            (da.date_time =
              this.searchshopForm.syear.split('')[0] +
              '-' +
              this.searchshopForm.smonth.split('')[0] +
              '-' +
              this.nowday);
        } else if (datecheck == '-9') {
          (da.time_way = 'month'),
            (da.date_time =
              this.searchshopForm.syear.split('')[0] +
              '-' +
              this.searchshopForm.smonth.split('')[0] +
              '-' +
              this.nowday)
        } else if (datecheck == '0') {
          (da.time_way = 'year'), (da.date_time = '')
        }
        console.log(da)
        this.$axios.post(
          domain.testUrl + '/admin/report_forms/statistics_goods_amount',
          qs.stringify(da),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        ).then(res => {
          console.log(res)
          if (res.data.code == '200') {
            let oChart2 = this.$echarts.init(document.getElementById('oChart2'))
            oChart2.setOption({
              tooltip: {
                trigger: 'axis'
              },
              legend: {
                data: [_this.fourName],
              },
              xAxis: [
                {
                  type: 'category',
                  boundaryGap: false,
                  data: this.shopxAxis
                }
              ],
              yAxis: {},
              series: [
                {
                  name: _this.fourName,
                  type: 'line',
                  smooth: true,
                  data: res.data.data
                }
              ]
            },window.onresize = oChart2.resize)
          } else {
            _this.sureLoading = false
            _this.$Notice.warning({ title: res.data.message })
          }
        })
      }
    },
    mounted() {
      this.getYearMonth()
    }
  }
</script>

<style>
  #main {
    display: block;
    margin: 0 auto;
  }

  .statistic-table .el-table {
    width: 100%;
  }

  .statistic-table .el-table .el-table__header {
    width: 100%;
  }

  .statistic-table .el-table .el-table__body {
    width: 100%;
  }

  #hf0,
  #hf1,
  #hf2,
  #hf3,
  #hf4,
  #hf5,
  #hf6,
  #hf7,
  #hf8 {
    display: inline-block;
    font-size: 0;
    color: #fff;
    line-height: 0;
    width: 0;
    height: 0;
  }
</style>


